/* medium devices <992px */
@media (max-width: 991px) { 
}

/* small devices <768px */
@media (max-width: 767px) { 
    div.imgbox {
        margin: 5px auto;
    }
}

/* tiny devices < 576px */
@media (max-width: 575px) { 
}


